.list-container {
  width: 98.3%;
  margin-bottom: 2em;

  .product-head {
    display: flex;
    justify-content: space-between;
    padding: 0.3em 0px;
    margin: 2em 0 1em;

    * {
      margin: 0;
    }

    h4 {
      font-weight: bold;
      color: #b48817;
      text-transform: capitalize;
    }

    button {
      background-color: #b48817;
      padding: 0 0.5em;

      a {
        color: #fff;
        text-decoration: none;
      }

      &:hover {
        background-color: #ca940c;
      }
    }
  }

  .product-body {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15em, 12.5em));
    grid-gap: 1.2em;
    margin-top: 0.5em;
    grid-auto-rows: auto;
    grid-auto-flow: dense;
    justify-content: space-between;
    place-content: center;

    .product-card {
      display: grid;
      grid-template-rows: 75% 25%;
      box-shadow: 1px 1px 5px 1px;

      .card-img {
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        height: 12.5em;
      }

      .card-body {
        margin: 0;
        padding: 0 0.3em;
        height: 4.6em;

        * {
          margin: 0;
          padding: 0;
        }

        h3 {
          font-size: 1em;
          margin-top: 0.2em;
          font-weight: bold;
          text-transform: uppercase;
          color: #a37a13d7;
        }

        h4,
        h6 {
          color: #666;
          text-transform: capitalize;
        }

        h4 {
          font-size: 1em;
          font-style: italic;
        }
        h6 {
          font-size: 0.8em;
        }
      }
    }
  }
}
@media screen and (max-width: 586px) {
  body.animsition {
    overflow-x: hidden !important;
  }
  .list-container .product-body {
    grid-template-columns:repeat(auto-fit, minmax(8em, 9.8em));
  
  }
}
@media screen and (max-width: 768px) {
  body.animsition {
    font-size: 70%;
    overflow-x: hidden !important;
  }
  .container {
    padding: 0;
  }
}
@media screen and (max-width: 425px) {
  body.animsition {
    font-size: 70%;
    overflow-x: hidden !important;
  }
  .container {
    padding: 0;
    max-width: 92% !important;
  }
}
